@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400&display=swap);
:root {
    --dark: rgb(29, 33, 37);
    --dark-secondary: #334e68;
    --dark-text: #8c9095;
    --light-text: #f0f4f8;
    --dark-green: rgb(36, 158, 115);
    --light-green: rgb(133, 221, 183);
    --blue: rgb(17, 190, 221);
    --light-purple: #e1c7e0;
    --dark-purple: #c054be;
}

body {
    background-color: rgb(29, 33, 37);
    background-color: var(--dark);
    font-family: "Lato";
    margin: 0;
    overflow-x: hidden;
}

.App-header {
    /* font-size: 20px; */
    color: #f0f4f8;
    color: var(--light-text);
    padding: 36px;
    padding-bottom: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.App-select {
    padding: 8px;
    width: 20%;
}

.App-escape {
    padding: 8px;
    text-align: right;
    color: #8c9095;
    color: var(--dark-text);
}

.App-data {
    padding: 8px;
    text-align: left;
    color: #8c9095;
    color: var(--dark-text);
}

.App-step {
    color: white;
}

.App-step-text-center {
    text-align: center;
}

::-webkit-scrollbar {
    width: 10px;
    height: 10px;
  }
  
  ::-webkit-scrollbar-thumb {
    background: #fff1;
    border-radius: 4px;
  }
  
  ::-webkit-scrollbar-track {
    background: none;
  }
.Bubbles-lyricRow {
    display: flex;
    justify-content: space-between;
    padding: 12px;
}

.Bubbles-ogLyric,
.Bubbles-kbLyric {
    padding: 12px;
    margin-top: 4px;
    background-color: #456381;
    border-radius: 16px;
    color: var(--light-text);
}

.Bubbles-kbLyric {
    text-align: right;
}

.Bubbles-arrow {
    align-self: center;
    color: var(--dark-text);
    min-width: 40px;
    text-align: center;
}

.Bubbles-layer {
    font-weight: 200;
    text-align: left;
    margin-top: 0;
    padding-left: 12px;
    color: var(--light-text);
    width: 300px;
}

b {
    font-weight: 200;
}

button {
    border: none;
    background-color: transparent;
    font-family: "Lato";
    color: var(--light-text);
    cursor: pointer;
}

.ogLyric-bad {
    background-color: var(--dark-purple);
    padding: 0px 4px;
    border-radius: 2px;
}

.kblyric-different {
    background-color: var(--light-green);
    padding-left: 4px;
    border-radius: 2px;
    color: var(--dark)
}

.kblyric-same.start {
    padding-left: 2px;
}
.IntroStep {
    animation: fadeIn linear 2s;
    text-align: left;
    margin: 0 15vw;
    padding: 40vh 0;
}

.header {
    font-size: 48px;
    font-weight: bold;
    margin-bottom: 24px;
}

.desc {
    font-size: 24px;
}

.center-image {
    margin: auto;
    display: block;
    padding: 16px;
}

.Step2 {
    margin: 10vh 0;
}

.Step2-p {
    font-size: 1.5em;
    margin: 0 15vw 24px 15vh;
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
